<template>
  <section>
    <my-header></my-header>
    <el-container>
      <el-aside class="menu" width="200px">
        <el-col>
          <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
            <el-menu-item index="1">
              <i class="el-icon-setting"></i>
              <span slot="title">我的博客</span>
            </el-menu-item>
          </el-menu>
        </el-col>
      </el-aside>
      <el-container>
        <el-main>
          <el-container>
            <el-aside width="150px">
              <el-tree :data="menuData" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
            </el-aside>
            <el-main>
              <el-card  v-for="item in data" v-on:click.native="go2WordPressDetail(item.articleId)">
                <h1>{{item.title}}</h1>
                <article v-html="item.content"></article>
                <p>{{item.userNickName}} {{item.createTime}}</p>
              </el-card>
            </el-main>
          </el-container>
        </el-main>
      </el-container>
    </el-container>
    <el-footer style="  height: 30px;">Designed & Powerd by Wallace Chan
      Copyright© 2018 陈国华
    </el-footer>
  </section>
</template>
<script>
import myHeader from './header.vue'
export default {
  components: {
    'my-header': myHeader
  },
  data() {
    return {
      menuData: [],
      defaultProps: {
        children: 'children',
        label: 'text'
      },
      data:[]
    }
  },
  methods: {
    go2WordPressDetail(articleId){
      console.log(articleId)
      this.$router.push({
        path:'wordpressDetail',
        query:{
          articleId
        }
      })
    },
    getArticle(categoryId){
      this.$utils.ajax({
          url: '/api/article/getArticleByCategoryId',
          data:{
            categoryId,
            page:1,
            rows:10
          },
          success:res=>{
            this.data=res.rows
          }
      })
    },
    handleNodeClick(data) {
      console.log(data);
      this.getArticle(data.categoryId)
    },
    getCategoryList() {
      this.$utils.ajax({
        url: '/api/article/getCategoryList',
        success: res => {
          this.menuData = res[0].children
        }
      })
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    }
  },
  mounted() {
    this.getCategoryList()
    this.getArticle()
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.el-container {
  height: 100%
}

.el-footer {
  font-size: 12px;
  color: #fff;
  background-color: @mainColor;
  line-height: 30px;
}

.el-header,
.el-footer {
  text-align: center;
}
.menu{
    background-color: @bgColor;
}
.el-aside {
  color: #333;
  text-align: center;
  line-height: 200px;
}

body>.el-container {
  margin-bottom: 40px;

}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

.el-card {
  cursor: pointer;
  // background-color: #fff;
  margin-bottom: 10px;
  // border-radius: 2px;
  // box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  // padding: 10px 20px;

  h1 {
    font-size: 24px;
    font-weight: 300;
    color: @mainColor;
  }

  article {
    color: @gary;
    margin-top: 10px;
    line-height: 24px;
    overflow: hidden;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 4;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /*! autoprefixer: on */
  }

  p {
    font-size: 12px;
    text-align: right;
    margin-top: 5px;
  }
}

</style>



