<template>
  <section>
    <my-header></my-header>
    <el-container>
      <el-main>
        <el-row> 评论：
        </el-row>
        <div class="comment" v-for="item in commentList">
          <span class="avatar" :style="`background-image:url(${item.userAvatar})`"></span>
          <div>
            <p>{{item.userNickName}}</p>
            <p>{{item.content}}</p>
          </div>
   <!--        <p>-{{item.createTime}}</p> -->
        </div>
        <el-row>
          <el-input type="textarea" :rows="2" placeholder="请输入评论" v-model="comment"> </el-input>
          <el-button v-on:click="doComment">评论</el-button>
        </el-row>
      </el-main>
    </el-container>
  </section>
</template>
<script>
import myHeader from './header.vue'
export default {
  data() {
    return {
      comment: '',
      commentList: []
    }
  },
  components: {
    'my-header': myHeader
  },
  mounted() {
    this.getCommentList()
  },
  methods: {
    doComment() {
      this.$utils.ajax({
        url: '/api/comment/addComment',
        data: {
          content: this.comment,
          articleId: this.$route.query.articleId
        },
        success: res => {
          if (res.status === 1) {
            this.getCommentList()
          }
        }
      })
    },
    getCommentList() {
      this.$utils.ajax({
        url: '/api/comment/getCommentList',
        data: {
          page: 1,
          rows: 10,
          articleId: this.$route.query.articleId
        },
        success: res => {
          this.commentList = res.rows
        }
      })
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.comment {
  display: flex;
  font-size: 14px;
  line-height:20px;
}

.avatar {
  display: block;
  width: 40px;
  height: 40px;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  margin-right: 10px;
}

</style>



